import React, { useContext, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { Context } from "../Context/MoviesContext";
import Footer from "../Footer/Footer";
import "./TV.css";

export default function TrendingTv() {
  let { trendingTV, MoreTV, incrementTV } = useContext(Context);
  const [data, setdata] = useState([]);
  const [Pages, setPages] = useState(2);
  useEffect(() => {
    if (MoreTV.length > 0) {
      setdata([...trendingTV, ...MoreTV]);
    }
  }, [incrementTV]);

  let incrementTVPages = () => {
    setPages(Pages + 1);
    console.log(Pages);
    incrementTV(Pages);
  };
  return (
    <>
      <section className="TVs">
        <div className="py-5">
          <div className="container">
            <div className="row g-3">
              {MoreTV.length > 0
                ? data.map((item, index) => (
                    <div className="col-md-3 trendTv" key={index}>
                      <Link to={`/details/${item.id}/tv`}>
                        <div className="TVImage">
                          <img
                            className="w-100"
                            src={`https://image.tmdb.org/t/p/original${item.poster_path}`}
                            alt=""
                          />
                        </div>
                      </Link>
                      <div className="tvName text-center mt-2">
                        <h4>{item.name}</h4>
                      </div>
                    </div>
                  ))
                : trendingTV.map((item, index) => (
                    <div className="col-md-3 trendTv" key={index}>
                      <Link to={`/details/${item.id}/tv`}>
                        <div className="TVImage">
                          <img
                            className="w-100"
                            src={`https://image.tmdb.org/t/p/original${item.poster_path}`}
                            alt=""
                          />
                        </div>
                      </Link>
                      <div className="tvName text-center mt-2">
                        <h4>{item.name}</h4>
                      </div>
                    </div>
                  ))}
            </div>
          </div>
          <div className="mx-4 text-center">
            <button onClick={incrementTVPages} className="Loadmore">
              Load more
            </button>
          </div>
        </div>
        <Footer />
      </section>
    </>
  );
}
