import React, { useState } from "react";
import { NavLink } from "react-router-dom";
import logo from "../images/tmovie.png";
import "../Navbar/Navbar.css";

export default function Navbar() {
  //change nanbar color when scrolling
  const [color, setcolor] = useState(false);

  let changeColor = () => {
    if (window.scrollY > 70) {
      setcolor(true);
    } else {
      setcolor(false);
    }
  };
  window.addEventListener("scroll", changeColor);

  return (
    <>
      <div className="container">
        <nav
          className={`navbar navbar-expand fixed-top ${
            color ? "bg-black" : "bg-transparent"
          }`}
        >
          <div className="container">
            <a className="navbar-brand fs-1" href="#">
              <img src={logo} width={50} height={50} alt="Logo" /> aMovies
            </a>

            <div className="collapse navbar-collapse">
              <ul className="navbar-nav mb-2 mb-lg-0">
                <li className="nav-item">
                  <NavLink to={"/"} className="nav-link" href="#">
                    Home
                  </NavLink>
                  <div className="NavLine"></div>
                </li>
                <li className="nav-item">
                  <NavLink to={"/movies"} className="nav-link" href="#">
                    Movies
                  </NavLink>
                </li>
                <li className="nav-item">
                  <NavLink to={"/tv"} className="nav-link" href="#">
                    TV Series
                  </NavLink>
                </li>
              </ul>
            </div>
          </div>
        </nav>
      </div>
    </>
  );
}
