import React, { useContext, useEffect, useState } from 'react';
import "./Movies.css";
import { Link } from 'react-router-dom';
import { Context } from '../Context/MoviesContext';
import Footer from '../Footer/Footer';


export default function TrendingMovies() {
  const [data, setdata] = useState([]);
  const [Pages, setPages] = useState(2);


  let {trendingMovies,MoreMovies,incrementMovies} = useContext(Context);
  useEffect(() => {
    if(MoreMovies.length>0)
    {
      setdata([...trendingMovies,...MoreMovies]);
    }
  }, [incrementMovies])
  
  let incrementMoviesPages = ()=>{
    setPages(Pages+1);
    incrementMovies(Pages);
  }    
  return (<>
<section className='movies'>
      <div className=" py-5">
        <div className='container '>
          <div className="row g-3">
            {MoreMovies.length>0?data.map((item,index)=> <div className='col-md-3 trendMovie' key={index}>
            <Link to={`/details/${item.id}/movie`}>
                <div className='movieImage'>
                  <img className='w-100' src={`https://image.tmdb.org/t/p/original${item.poster_path}`} alt="" />
                </div>
            </Link>
                <div className="movieName text-white text-center mt-2">
                  <h4>{item.title}</h4>
                </div>
            </div>):trendingMovies.map((item,index)=> <div className='col-md-3 trendMovie' key={index}>
            <Link to={`/details/${item.id}/movie`}>
                <div className='movieImage'>
                  <img className='w-100' src={`https://image.tmdb.org/t/p/original${item.poster_path}`} alt="" />
                </div>
            </Link>
                <div className="movieName text-white text-center mt-2">
                  <h4>{item.title}</h4>
                </div>
            </div>)}
          </div>
        </div>
        <div className="mt-5 text-center">
          <button onClick={incrementMoviesPages} className='Loadmore'>Load more</button>
        </div>
      
       
      </div>
      <Footer/>
    </section>
  </>)
}
