import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom';
import {Swiper, SwiperSlide}from 'swiper/react';


export default function SimilarMovies() {
    const [similar, setsimilar] = useState([])
        let {id,mediaType} = useParams();

        useEffect(() => {
          if(mediaType==='movie')
          {
            getSimilar(id,'movie');
          }
          else if (mediaType==='tv')
          {
            getSimilar(id,'tv');
          }

        }, [id,mediaType])
        

    let getSimilar = async(id,media)=>{
        let {data} = await axios.get(`https://api.themoviedb.org/3/${media}/${id}/similar?api_key=f9250279af7dc881bf30759ec5164577&language=en-US&page=1`)
        setsimilar(data.results);
    }
  return (<>
     <Swiper breakpoints={{
                    0:{
                      slidesPerView:1.2,
                      spaceBetween:10},
                    480:{
                      slidesPerView:2.2,
                      spaceBetween:10},
                    768:{
                      slidesPerView:3.5,
                      spaceBetween:10},
                    1024:{
                      slidesPerView:5.5,
                      spaceBetween:10},
                    1280:{
                      slidesPerView:6.2,
                      spaceBetween:10},
                      }}>
                         {similar?similar.map((item,index)=><SwiperSlide key={index}>
      <div className='movieSlider'>
      <Link to={`/details/${item.id}/${mediaType}`}>
        <img className='w-100' src={`https://image.tmdb.org/t/p/original${item.poster_path}`} alt="" />
        <div className="movieLayer ">
          <i className="youtubeIcon fa-brands fa-youtube"></i>
        </div>
      </Link>
      </div>
      <h5 className='text-white text-center py-3'>{item.title}</h5>
  </SwiperSlide>):''}
              </Swiper>
  
  </>)
}
