import React, { useContext, useEffect, useState } from "react";
import headerImage from "../images/footer-bg.jpg";
import { Context } from "../Context/MoviesContext";
import "./TV.css";
import TrendingTv from "./TrendingTv";
import { Link } from "react-router-dom";
import Footer from "../Footer/Footer";

export default function TV() {
  const [Text, setText] = useState(null);
  let { getTvWord, SearchTV } = useContext(Context);

  useEffect(() => {
    window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
  }, [SearchTV]);

  let Search = (e) => {
    e.preventDefault();
    if (Text != null) {
      getTvWord(Text);
    }
  };
  let getText = (e) => {
    setText(e.target.value);
  };

  return (
    <>
      <div
        className="headerImage text-center py-5"
        style={{ backgroundImage: `url(${headerImage})` }}
      >
        <h3 className="text-white pt-5 pb-3 ">TV Series</h3>
      </div>
      <div className="toptv">
        <div className="container">
          <form onSubmit={Search}>
            <div className="searchBtn">
              <input
                onChange={getText}
                className="bg-black text-muted"
                type="text"
                placeholder="Enter Keyword"
              />
              <button className="btn px-4">Search</button>
            </div>
          </form>
        </div>
        {SearchTV.length > 0 ? (
          <section className="TVs">
            <div className="py-5">
              <div className="container">
                <div className="row g-3">
                  {SearchTV
                    ? SearchTV.map((item, index) => (
                        <div className="col-md-3" key={index}>
                          <Link to={`/details/${item.id}/tv`}>
                            <div className="TVImage">
                              <img
                                className="w-100"
                                src={`https://image.tmdb.org/t/p/original${item.poster_path}`}
                                alt="image"
                              />
                            </div>
                          </Link>
                          <div className="movieName text-white text-center mt-2">
                            <h4>{item.name}</h4>
                          </div>
                        </div>
                      ))
                    : ""}
                </div>
              </div>
            </div>
            <Footer />
          </section>
        ) : (
          <TrendingTv />
        )}
      </div>
    </>
  );
}
